*{
    padding: 0px;
    margin: 0%;
}

.header{
    width: 100%;
    position: fixed;
    top: 0em;
    left: 0em;
    z-index: 1;

    /* border: 2px solid black; */
}

html body {
    padding-top: 8%;
    height: auto;
   }

.headerContent{
    height: 50px;
    z-index: 1;
    /* position: fixed; */
    background-color:palevioletred;
    display: flex;
    align-items: center;
    margin-bottom: 1em;
    /* border: 2px solid white; */

    /* margin-top: -20em; */
}

.logo{
    color: whitesmoke;  
    flex-grow: 1;
    padding-left: 10px;
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.articles{
    padding-right: 50px;
}

.link{
    padding: 10px;
    text-decoration: none;
    color: whitesmoke;
}